.filter {
    &-box {
        &-filter {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            margin: -10px;
            &-item {
                padding: 10px;
            }
        }
    }
    &-box-sort-text {
        font-size: 14px;
        font-weight: $fw-bold;
    }
    &-btn {
        border: 1px solid rgba(128, 137, 150, 0.2);
        font-weight: $fw-semibold;
        font-size: $fs-15;
        &-s1 {
            border: 0;
            padding-right: 0;
            padding-left: 0;
        }
    }
}