.header {
    &-section {
        &.has-header-main {
            padding-top: 68px;
        }
    }
    &-search {
        &-form {
            display: none;
            .form-control-s1 {
                border-color: rgba($base-color, 0.1);
                &:focus {
                    border-color: rgba($base-color, 0.3);
                }
            }
        }
        &-mobile {
            .card-generic {
                min-width: 300px;
                padding: 15px;
                margin-top: 10px!important;
                margin-right: -10px !important;
            }
        }
    }
    &-mobile-action {
        display: flex;
        align-items: center;
    }
    &-menu {
        overflow-x: auto;
    }
}

//Overlay
.overlay {
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba($dark, 0.6);
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
}


@include media-breakpoint-up(xl){
    .header {
        &-search-form {
            display: block;
            width: 46%;
            padding-left: 40px;
            &-s2 {
                width: 70%;
                padding-left: 60px;
            }
        }
    }
   
}

@include media-breakpoint-up(lg){
    .header {
        &-section {
            &.has-header-main {
                padding-top: 88px;
            }
        }
        &-mobile-action {
            .header-search-mobile,
            .header-mobile-wallet {
                display: none;
            }
        }
    }
   
}

