.nav {
    &-tabs {
        &-mobile {
            display: none;
        }
        &-s1 {
            border-bottom-color: $base-300;
            .nav {
                &-item {
                    margin-right: 25px;
                }
                &-link {
                    position: relative;
                    border: 0;
                    padding-right: 0;
                    padding-left: 0;
                    font-weight: $fw-semibold;
                    color: $headding-color;
                    padding-bottom: 13px;
                    padding-top: 0;
                    &::after {
                        position: absolute;
                        content: "";
                        left: 0;
                        right: 0;
                        bottom: 0;
                        height: 2px;
                        background-color: $primary;
                        width: 100%;
                        @include transition(0.2s);
                        @include transform(scale(0));
                    }
                    &.active {
                        color: $primary;
                        background-color: transparent;
                        &::after {
                            @include transform(scale(1));
                        }
                    }
                }
           }
   
       }
       &--s1 {
           border-bottom: 0;
           .nav {
            &-link {
                padding: 0;
                &::after {
                    display: none;
                }
            }
           }
       }
       &-s2 {
            border-bottom: 0;
            margin: -5px;
            .nav {
                &-item {
                    padding: 5px;
                    width: 50%;
                }
                &-link {
                    border: 1px solid rgba(50, 72, 104, 0.2);
                    padding: 10px;
                    @include border-radius(0.375rem);
                    margin-bottom: 0;
                    color: $headding-color;
                    font-weight: $fw-semibold;
                    width: 100%;
                    .nav-link-icon {
                        font-size: $fs-7;
                    }
                    .icon-svg {
                        height: 30px;
                    }
                    &::after {
                        display: none;
                    }
                    &.active {
                        border-color: $primary;
                        background-color: $base-600;
                    }
                    &:hover:not(.active) {
                        border-color: rgba(50, 72, 104, 0.4);
                    }
                }
            }
        }
        &--s2 {
            .nav{
                &-link {
                    &-title {
                        font-size: 13px;
                    }
                }
            }
        }
       &-s3 {
            border-bottom: 0;
            margin: -5px;
            .nav {
                &-item {
                    padding: 5px;
                }
                &-link {
                    @include box-shadow(0 1px 5px rgba(0,0,0, .1));
                    padding: 5px 20px;
                    @include border-radius(0.375rem);
                    margin-bottom: 0;
                    border: 0;
                    font-weight: $fw-medium;
                    color: $headding-color;
                    background-color: $white;
                    &::after {
                        display: none;
                    }
                    &:hover {
                        color: $primary;
                    }
                    &.active {
                        background-color: $primary;
                        color: $white;
                    }
                }
            }
        }
        &-s4 {
            border-bottom: 0;
            .nav {
                &-item {
                    margin-bottom: 7px;
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
                &-link {
                    border: 0;
                    padding-right: 0;
                    padding-left: 0;
                    font-weight: $fw-semibold;
                    color: $headding-color;
                    padding-bottom: 0;
                    padding-top: 0;
                    font-size: 15px;
                    &.active {
                        color: $primary;
                        background-color: transparent;
                    }
                }
           }
   
       }
    }
}

.tab-content {
    &-desktop {
        display: none;
    }
}

// Responsive breakpoint
@include media-breakpoint-up(sm){
    .nav {
       &-tabs {
           &-s2 {
             margin: -10px;
             .nav {
                 &-item {
                     padding: 10px;
                     width: auto;
                 }
                 &-link {
                     width: auto;
                     padding: 1.25rem 1.563rem;
                     .icon-svg {
                         height: auto;
                     }
                   }
               }
           }
           &--s2 {
             .nav {
                 &-link {
                     width: 135px;
                     padding-right: 0;
                     padding-left: 0;
                     &-title {
                         font-size: 14px;
                     }
                 }
             }
         }
       }
    }
 }

@include media-breakpoint-up(lg) {
    .nav {
        &-tabs {
            &-mobile {
                display: flex;
            }
        }
    }
    .tab-content {
        &-desktop {
            display: block;
        }
        &-mobile {
            display: none;
        }
    }
}

@include media-breakpoint-up(xl) {
    .nav {
        &-tabs {
            &--s2 {
                .nav {
                    &-link {
                        width: 150px;
                        padding-right: 0;
                        padding-left: 0;
                    }
                }
            }
        }
    }
}
