.history {
    &-panel {
        border-bottom: 1px solid $base-100;
        padding-bottom: 10px;
        margin-bottom: 10px;
        p {
            width: 100%;
        }
    }
}

@include media-breakpoint-up(md) {
    .history {
        &-panel {
            p {
                width: auto;
            }
        }
    }
}