.sidebar {
    &-widget{
        margin-bottom: 32px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &-text,
    &-btn-link {

        font-size: 15px;
    }
    &-btn-wrap {
        display: block;
    }
    &-user-mobile {
        position: fixed;
        background-color: $white;
        width: 290px;
        z-index: 1010;
        top: 0;
        left: 0;
        height: 100%;
        overflow: auto;
        padding: 80px 20px 20px 20px;
        @include box-shadow(0 0 10px 2px rgba(0, 0, 0, 0.2));
        @include transition(0.4s);
        @include transform(translateX(-100%));
        .menu-toggler-user-close {
            position: absolute;
            top: 20px;
            right: 20px;
        }
        &.active {
            @include transform(translateX(0));
        }
    }
    &-drop {
        .card-generic {
            padding: 20px;
        }
        .nav {
            &-tabs {
                flex-direction: column;
            }
            &-item,
            &-link {
                width: 100%;
                text-align: left;
            }
        }
    }
}

// Responsive breakpoint
@include media-breakpoint-up(lg) {
    .sidebar {
        &-head {
            &-title {
                display: block;
            }
            &-btn {
                display: none;
            }
        }
        &-drop {
            display: none;
        }
        &-btn-wrap {
            display: none;
        }
        &-user-mobile {
            position: relative;
            background-color: transparent;
            width: auto;
            z-index: inherit;
            top: auto;
            left: auto;
            height: auto;
            overflow: inherit;
            padding: 0;
            @include box-shadow(none);
            @include transform(translateX(0));
        }
    }
}