.swiper-carousel {
    margin: -15px;
    .swiper-slide {
        padding: 15px;
    }
}

.swiper {
    &-wrapper {
        padding-bottom: 1.25rem;
    }
    &-pagination-bullet {
        @include border-radius(2px);
        width: 20px;
        height: 4px;
        &-active {
          background: $primary;  
        }
        &s.swiper-pagination-horizontal {
            bottom: 18px;
        }
    }
    &-button-prev, 
    &-button-next {
        width: 40px;
        height: 40px;
        @include border-radius(50%);
        background-color: $white;
        @include box-shadow(0 2px 6px rgba(0,0,0, 0.1));
        @include transition(0.3s);
        &:after {
            font-size: 20px;
            color: $headding-color;
            font-family: $ni-font;
        }
        &:hover {
            background-color: $primary;
            &:after {
                color: $white;
            }
        }
    }
    &-button-prev {
        left: 2px;
        &::after {
            content: $ni-chevron-left;
        }
    }
    &-button-next {
        right: 2px;
        &::after {
            content: $ni-chevron-right;
        }
    }
}
